import React from 'react';

interface GoodsListProps {
  goods: Goods[];
}

export interface Goods {
  [key: string]: string | number;
}

export const GoodsList: React.FC<GoodsListProps> = ({ goods }) => {
  return (
    <ul>
      {goods.map((item: Goods) => (
        <li
          key={item.name}
          style={{ color: `${item.color}` }}
        >
          {item.name}
        </li>
      ))}
    </ul>
  );
};
