import React, { useState } from 'react';
import { getGoods } from './api/api';
import './App.css';
import { GoodsList, Goods } from './components/GoodsList';


const App: React.FC = () => {
  const [goods, setGoods] = useState([]);
  const [isLoading, setLoading] = useState(false);

  const loadGoods = ({ target }: any): void => {
    setLoading(true);

    getGoods()
      .then(items => {
        if (target.name === 'first-5') {
          setGoods(items.slice(0, 5));
        } else if (target.name === 'only-red') {
          setGoods(items.filter((item: Goods) => item.color === 'red'));
        } else {
          setGoods(items);
        }

        setLoading(false);
      });
  };

  return (
    <div className="good-list">
      <h1>Dynamic list of Goods</h1>
      <button
        type="button"
        className="waves-effect waves-light indigo accent-1 btn"
        onClick={(e) => loadGoods(e)}
      >
        Load All goods
      </button>
      <button
        type="button"
        className="waves-effect waves-light indigo accent-1 btn"
        name="first-5"
        onClick={(e) => loadGoods(e)}
      >
        Load 5 first goods
      </button>
      <button
        type="button"
        className="waves-effect waves-light indigo accent-1 btn"
        name="only-red"
        onClick={(e) => loadGoods(e)}
      >
        Load red goods
      </button>
      {isLoading && <p>Loading data...</p>}
      <GoodsList goods={goods} />
    </div>
  );
};

export default App;
